/* added permalinks ul from footer.jsx  */

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after {
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* ==================== ME ===================== */

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 18rem;
    height: 18rem;
    position: absolute;
    left: calc(50% - 9rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 1rem 1.8rem 1.8rem 1.8rem;
}

/* ================= SCROLL DOWN ==================== */

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* =================== MEDIA QUERIES (MEDIUM DEVICES)======================*/

@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    };
}

/* =================== MEDIA QUERIES (SMALL DEVICES)======================*/

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header__socials, 
    .scroll__down {
        display: none;
    }
}